
import { toyService } from '../../services/toy.service.js'
import { showSuccessMsg, showErrorMsg } from '../../services/event-bus.service.js'

export function loadToys() {
    return dispatch => {
        toyService.query()
            .then(toys => {
                console.log('Toys from DB:', toys);
                dispatch({
                    type: 'SET_TOYS',
                    toys
                })
            })
    }
}

export function addToy(toy) {
    return dispatch => {
        toyService.save(toy)
            .then(savedToy => {
                console.log('Added Toy', savedToy);
                dispatch({
                    type: 'ADD_TOY',
                    toy: savedToy
                })
                showSuccessMsg('Toy added')
            })
            .catch(err => {
                console.error('Error:', err)
                showErrorMsg('Cannot add toy')
            })
    }
}

export function editToy(toyToSave) {
    return dispatch => {
        toyService.save(toyToSave)
            .then(savedToy => {
                console.log('Updated Toy:', savedToy);
                dispatch({
                    type: 'UPDATE_TOY',
                    toy: savedToy
                })
                showSuccessMsg('Toy updated')
            })
            .catch(err => {
                console.error('Error:', err)
                showErrorMsg('Cannot update toy')
            })
    }
}

export function filtering(filterBy) {
    return dispatch => {
            toyService.query(filterBy)
                .then(toys => {
                    console.log('Toys from DB:', toys);
                    dispatch({
                        type: 'SET_TOYS',
                        toys
                    })
                    dispatch({
                        type: 'FILTER_TOY',
                        filterBy
                    })
                })
        }
}

export function removeToy(toyId) {
    return dispatch => {
        toyService.remove(toyId)
            .then(() => {
                dispatch({
                    type: 'REMOVE_TOY',
                    toyId
                })
                console.log('Deleted Succesfully!')
                showSuccessMsg('Toy removed')
            })
            .catch(err => {
                console.error('Error:', err)
                showErrorMsg('Cannot remove toy')
            })
    }
}







