
import { utilService } from './util.service.js'

const axios = require('axios').default;

export const toyService = {
    query,
    getById,
    save,
    remove,
    getEmptyToy
}

const Axios = axios.create({withCredentials: true})

// Reuse const
// const BASE_URL = `http://localhost:3001/api/toy/`   
const BASE_URL = (process.env.NODE_ENV === 'production')
    ? '/api/toy/'
    : 'http://localhost:3030/api/toy/';

// Request (Rest:GET) all Data from Backend
function query(filterBy = {}) {
    let queryString = JSON.stringify(filterBy)
    return axios.get(BASE_URL + 'filter/' + queryString)
        .then(res => res.data)
}

// Request (Rest:GET) get a Bug from Backend
function getById(toyId) {
    return Axios.get(BASE_URL + toyId) // Example: /api/toy/u101
        .then(res => res.data)
}

// Request (Rest:DELETE) delete a Bug from Backend
function remove(toyId) {
    return Axios.delete(BASE_URL + toyId).then(res => res.data)
}

// Request (Rest:PUT & POST) update or add new Bug from Backend
function save(toy) {
    if (toy._id) {
        return Axios.put(BASE_URL + toy._id, toy) // the 'toy' is the reponse
            .then(res => res.data)
    } else {
        return Axios.post(BASE_URL, toy)
            .then(res => res.data)
    }
}

function getEmptyToy() {
    return {
        name: 'Toy No:' + (Date.now() % 1000),
        img: utilService.getRandomIntInclusive(0, 13),
        price: utilService.getRandomIntInclusive(50, 150),
        labels: ["Doll", "Battery Powered", "Baby"],
        createdAt: Date.now() - utilService.getRandomIntInclusive(10000000, 1000000000),
        inStock: utilService.getRandomIntInclusive(1, 10) <= 5 ? true : false
    }
}