// import { storageService } from './async-storage.service.js'

const axios = require('axios').default;

export const userService = {
   login,
   logout,
   signup,
   getLoggedinUser,
   userQuery,
   remove
}

// const BASE_URL = `http://localhost:3001/api/`   

const Axios = axios.create({withCredentials: true})

const BASE_URL = (process.env.NODE_ENV === 'production')
    ? '/api/'
    : 'http://localhost:3030/api/';

// Window.sessionStorage:
// sessionStorage is similar to localStorage; 
// the difference is that while data in localStorage doesn't expire, 
// data in sessionStorage is cleared when the page session ends.

// Get loged user from sessionStorage
function getLoggedinUser() {
   const user = JSON.parse(sessionStorage.getItem('loggedinUser'))
   return user
}

// Request (Rest:POST) login
function login(credentials) {
   // waiting from the user if success to login
   return Axios.post(BASE_URL + 'login', credentials)
      .then(res => res.data)
      .then(user => {
         // set the user at sessionStorage
         console.log('user', user);
         sessionStorage.setItem('loggedinUser', JSON.stringify(user))
         
         return user
      })
}

// Request (Rest:POST) signup
function signup(credentials) {
   // waiting from the user if success to login
   return Axios.post(BASE_URL + 'signup', credentials)
      .then(res => res.data)
      .then(user => {
         // set the user at sessionStorage
         sessionStorage.setItem('loggedinUser', JSON.stringify(user))
         return user
      })
}

// Request (Rest:POST) logout
function logout() {
   return Axios.post(BASE_URL + 'logout')
      .then(() => {
         // remove the user at sessionStorage
         sessionStorage.removeItem('loggedinUser')
      })
}

function userQuery() {
   return Axios.get(BASE_URL + 'users')
      .then(res => res.data)
}

function remove(userId) {
   return Axios.delete(BASE_URL + 'users/' + userId)
      .then(res => res.data)
}